import React from 'react';
import PropTypes from 'prop-types';
import {
    Card,
    FileInput,
    FormGroup,
    Icon,
    InputGroup,
    Radio,
    RadioGroup,
    Spinner,
    Text,
    Elevation,
    Intent, ButtonGroup, Button, Popover, PopoverInteractionKind, Position
} from '@blueprintjs/core'; 
import queryString from "query-string";
import _isEqual from "lodash/isEqual";
import _omit from "lodash/omit";
import _has from "lodash/has";
import _isEmpty from 'lodash/isEmpty';
import _find from 'lodash/find';
import {Link, withRouter, Route} from "react-router-dom";
import {connect} from "react-redux";
import {DeploymentTable} from './../Table';
import { bindActionCreators } from 'redux';
import Can from "../../../helpers/Can";
import {ADD, DELETE} from "../../../api/constants/Actions";
import {CENTER} from "../../../api/constants/Entities";
import {CENTERS} from "../../../api/constants/Pages";
import {isFormValid} from "../../../helpers";
import {ADD_CENTER, DELETE_CENTER, EDIT_CENTER} from "../../../helpers/constants";
import {deleteCenter, editCenter, loadCenter} from "../../../actions/CentersActions";
import Toaster from "../../Common/Toaster";
import {DateInput} from "@blueprintjs/datetime";
import {MultipleLongList} from "../../Common/Lists";
import format from 'date-fns/format';


class Center extends React.Component{

    state = {
        confirmDelete: false,
        confirmUpdate: false,

        center: {},
        centerBuffer: {},
        centerRaw: {},
        sketchFile: null,

        deployment: {
            startDate: new Date(),
            endDate: new Date(),
            printers: [],
            machines: []
        }
    };

    componentDidMount(){
        const {centerId, authUser: {token}, loadCenter} = this.props;
        console.log("selected " , centerId);

        if(centerId){
            loadCenter(token, centerId);
        }
    }

    static getDerivedStateFromProps(nextProps, prevState){
        const {center: nextCenter} = nextProps;
        const {center: prevCenter} = prevState;

        console.log("get", prevCenter, nextCenter)

        if(_isEqual(nextCenter, prevCenter))
            return null;

        const updatedCenter = _isEqual(nextCenter.id, prevCenter.id) ? {...nextCenter, ...prevCenter} : {...nextCenter}

        return {center: {...updatedCenter}, centerBuffer: {...nextCenter}, centerRaw: {...nextCenter}};
    }

    componentDidUpdate(prevProps){
        const {centerId: prevCenterId} = prevProps;
        const {authUser: {token}, loadCenter, centerId: currentCenterId} = this.props;
        const {center} = this.state;

        if(!_isEqual(prevCenterId, currentCenterId)){
            loadCenter(token, currentCenterId);
        }

        const {OPERATION_SUCCESSFUL: OLD_OPERATION_SUCCESSFUL, OPERATION_FAILED: OLD_OPERATION_FAILED} = prevProps;
        const {OPERATION_SUCCESSFUL, OPERATION_FAILED} = this.props;

        if(!_isEqual(OLD_OPERATION_SUCCESSFUL, OPERATION_SUCCESSFUL)){
            const {action} = OPERATION_SUCCESSFUL;

            if(action === EDIT_CENTER){
                loadCenter(token, currentCenterId);
                Toaster.show({
                    message: `${center.name} Successfully Updated `,
                    intent: Intent.SUCCESS,
                    icon: 'tick' });
            }

            if(action === DELETE_CENTER){
                Toaster.show({
                    message: `${center.name} Successfully Removed `,
                    intent: Intent.SUCCESS,
                    icon: 'tick' });
            }
        }

        if(!_isEqual(OPERATION_FAILED, OLD_OPERATION_FAILED)){
            const {action} = OPERATION_FAILED;
            if(action === EDIT_CENTER){
                Toaster.show({
                    message: `Could Not Update ${center.name} `,
                    intent: Intent.DANGER,
                    icon: 'error'
                });
            }

            if(action === DELETE_CENTER){
                Toaster.show({
                    message: `Could Not Remove ${center.name} `,
                    intent: Intent.DANGER,
                    icon: 'error'
                });
            }
        }
    }

    _handleDeleteCenter = () => {
        const {centerBuffer: {id}} = this.state;
        const {deleteCenter, authUser: {token}} = this.props;
        deleteCenter(token, id);
        this.setState({confirmDelete: false});
    }

    _handleUpdateCenter = () => {
        const {center: {name, code, ghana_post_gps, longitude, latitude, sketchFile, contact_name, contact_phone, 
            preferred_network, backup_network, recomended}, centerBuffer: {id}, deployment} = this.state;
        const {editCenter, authUser: {token}} = this.props;

        let data = new FormData();
        data.append("name", name);
        data.append("code", code);

        data.append("ghana_post_gps", ghana_post_gps);
        data.append("longitude", longitude);
        data.append("latitude", latitude);
        data.append("sketch", sketchFile);

        data.append("contact_name", contact_name);
        data.append("contact_phone", contact_phone);

        data.append("preferred_network", preferred_network);
        data.append("backup_network", backup_network);
        data.append("recomended", recomended);

        editCenter(token, id, data);
        this.setState({confirmUpdate: false});
    }

    _handleInputChange = (name, value) => {
        
        const newCenter = {...this.state.center, [name]: value};
        console.log(name, value, newCenter);
        this.setState({center: newCenter});
    }

    _isFormValid = () => {
        const {center: {name, code, ghana_post_gps, longitude, latitude}} = this.state;

        let required = {name, code, ghana_post_gps, longitude, latitude};

        return isFormValid(required);
    }

    _isDeploymentFormValid = () => {
        const {deployment: {startDate, endDate, printers, machines}} = this.state;

        let required = {startDate: format(startDate, "X"), endDate: format(endDate, "X")};

        console.log(required);

        return isFormValid(required) && !_isEmpty(printers) && !_isEmpty(machines);
    }

    render(){

        const {confirmDelete, confirmUpdate, center, centerBuffer, deployment} = this.state;
        const {loadingCenter, machines: machinesP, printers: printersP, editingCenter} = this.props;

        const disableSaveButton = !this._isFormValid() || _isEqual(center, centerBuffer);

        const disableDeploymentButton = !this._isDeploymentFormValid();

        const printers = printersP.map(({id, serial}) => ({id, name: serial}));
        const machines = machinesP.map(({id, name}) => ({id, name}));

        return (
            <Card className={"Details"}>
                <header className="Header">
                    <div style={{display: 'flex', alignItems: 'center'}}>
                        {loadingCenter ? <Spinner className="bp3-small"/> : <Icon icon="map-marker"/>}
                        <Icon icon="blank"/>
                        <Text className="devices__text">
                            Center <small className="bp3-text-muted">{center.id && `| ${center.name}`}</small>
                        </Text>
                    </div>
                    {
                        this.props.center.id && (
                            <ButtonGroup>
                                <Popover disabled={disableSaveButton} isOpen={confirmUpdate} interactionKind={PopoverInteractionKind.CLICK} position={Position.BOTTOM}>
                                    <Button 
                                        disabled={disableSaveButton} 
                                        loading={editingCenter}
                                        onClick={() => this.setState({confirmUpdate: true})} 
                                        icon={"floppy-disk"} 
                                        intent={Intent.WARNING} />
                                    <div style={{ padding: ".5rem", display: "flex", flexDirection: "column" }}>
                                        <header>Are you sure?</header>
                                        <div style={{ display: "flex", justifyContent: "space-evenly", marginTop: ".5rem" }}>
                                            <Button onClick={() => this._handleUpdateCenter()} small text="yes" intent={Intent.SUCCESS}/>
                                            <Button onClick={() => this.setState({confirmUpdate: false})} small text="no" intent={Intent.DANGER}/>
                                        </div>
                                    </div>
                                </Popover>
                                <Popover isOpen={confirmDelete} interactionKind={PopoverInteractionKind.CLICK} position={Position.BOTTOM}>
                                    <Button onClick={() => this.setState({confirmDelete: true})} icon={"trash"} intent={Intent.DANGER} />
                                    <div style={{ padding: ".5rem", display: "flex", flexDirection: "column" }}>
                                        <header>Are you sure?</header>
                                        <div style={{ display: "flex", justifyContent: "space-evenly", marginTop: ".5rem" }}>
                                            <Button onClick={() => this._handleDeleteCenter()} small text="yes" intent={Intent.SUCCESS}/>
                                            <Button onClick={() => this.setState({confirmDelete: false})} small text="no" intent={Intent.DANGER}/>
                                        </div>
                                    </div>
                                </Popover>
                            </ButtonGroup>
                        )
                    }
                </header>

                {
                    !center.id 
                    ? (
                        <div className="bp3-non-ideal-state">
                            <div className="bp3-non-ideal-state-visual">
                                <span className="bp3-icon bp3-icon-map-marker"></span>
                            </div>
                            <h4 className="bp3-heading">No Center Selected</h4>
                            <div>Select any center to view details</div>
                            {/* <button className="bp3-button bp3-intent-primary">Create</button> */}
                        </div>
                    )
                    :(
                        <div className="Content">

                            <section className="Basics">
                                <header className="Header">Basic Information</header>
                                <section className="Content__Simple">

                                    <div className={'mm-input-group'}>
                                        <label className={"mm-label"} htmlFor={"name"}> Name </label>
                                        <InputGroup className={"mm-input"} 
                                            onChange={({target: {name, value}}) => this._handleInputChange(name, value)}
                                            name="name"
                                            value={center.name || ''} id="name" />
                                    </div>

                                    <div className={'mm-input-group'}>
                                        <label className={"mm-label"} htmlFor={"code"}> Code </label>
                                        <InputGroup className={"mm-input"} 
                                            onChange={({target: {name, value}}) => this._handleInputChange(name, value)}
                                            name="code"
                                            value={center.code || ''} id="code" />
                                    </div>

                                </section>
                            </section>

                            <section className="Location">
                                <header className="Header">Location</header>
                                <section className="Content__Simple">
                                    <div className={"Column"}>
                                        <div className={'mm-input-group'}>
                                            <label className={"mm-label"} htmlFor={"region"}> Region </label>
                                            <InputGroup className={"mm-input"}
                                                        id="region"
                                                        onChange={({target: {name, value}}) => this._handleInputChange(name, value)}
                                                        name="region_id"
                                                        value={center.region_id || ""} />
                                        </div>

                                        <div className={'mm-input-group'}>
                                            <label className={"mm-label"} htmlFor={"district"}> District </label>
                                            <InputGroup className={"mm-input"}
                                                        id="district"
                                                        onChange={({target: {name, value}}) => this._handleInputChange(name, value)}
                                                        name="district_id"
                                                        value={center.district_id || ""} />
                                        </div>

                                        <div className={'mm-input-group'}>
                                            <label className={"mm-label"} htmlFor={"gpgps"}> Ghana Post GPS </label>
                                            <InputGroup className={"mm-input"}
                                                        id="gpgps"
                                                        onChange={({target: {name, value}}) => this._handleInputChange(name, value)}
                                                        name="ghana_post_gps"
                                                        value={center.ghana_post_gps || ""} />
                                        </div>

                                        <div className={'mm-input-group'}>
                                            <label className={"mm-label"} htmlFor={"long"}> Longitude </label>
                                            <InputGroup className={"mm-input"}
                                                        id="long"
                                                        onChange={({target: {name, value}}) => this._handleInputChange(name, value)}
                                                        name="longitude"
                                                        value={center.longitude || ""}/>
                                        </div>

                                        <div className={'mm-input-group'}>
                                            <label className={"mm-label"} htmlFor={"lat"}> Latitude </label>
                                            <InputGroup className={"mm-input"}
                                                        id="lat"
                                                        onChange={({target: {name, value}}) => this._handleInputChange(name, value)}
                                                        name="latitude"
                                                        value={center.latitude || ""} />
                                        </div>

                                        <div className={'mm-input-group'}>
                                            <label className={"mm-label"} htmlFor={"sketch"}> Arrangement Sketch </label>
                                            <FileInput id={"sketch"}
                                                    className={"mm-input"}
                                                    text="Choose file..."
                                                    onChange={({target: {files}}) => {
                                                        const url = URL.createObjectURL(files[0]);
                                                        this._handleInputChange("sketch", url);
                                                    }} />
                                        </div>
                                    </div>

                                    <div className="Column">
                                        <Card elevation={Elevation.ONE} className={"Location__Sketch__Preview"}>
                                            <img style={{ objectFit: "contain", with: 'calc(100% - .4rem)', height: 'calc(100% - .4rem)' }} src={this.state.sketch || center.sketch}/>
                                        </Card>
                                    </div>
                                </section>
                            </section>

                            <section className="Contact">
                                <header className="Header">Contact Person</header>
                                <section className="Content__Simple">
                                    <div className={'mm-input-group'}>
                                        <label className={"mm-label"} htmlFor={"contact-name"}> Full name </label>
                                        <InputGroup className={"mm-input"}
                                                    id="contact-name"
                                                    onChange={({target: {name, value}}) => this._handleInputChange(name, value)}
                                                    name="contact_name"
                                                    value={center.contact_name || ""} />
                                    </div>

                                    <div className={'mm-input-group'}>
                                        <label className={"mm-label"} htmlFor={"contact-number"}> Phone number </label>
                                        <InputGroup className={"mm-input"}
                                                    id="contact-number"
                                                    onChange={({target: {name, value}}) => this._handleInputChange(name, value)}
                                                    name="contact_phone"
                                                    value={center.contact_phone || ""} />
                                    </div>
                                </section>
                            </section>

                            <section className="Recommendations">
                                <header className="Header">Site survey recommendation</header>
                                <section className="Content__Simple">
                                    <div className="Column">

                                        <div className={'mm-input-group'}>
                                            <label className={"mm-label"} htmlFor={"preferred-network"}> Preferred Network </label>
                                            <InputGroup className={"mm-input"}
                                                        id="preferred-network"
                                                        onChange={({target: {name, value}}) => this._handleInputChange(name, value)}
                                                        name="preferred_network"
                                                        value={center.preferred_network || ""} />
                                        </div>
                                    </div>

                                    <div className="Column">
                                        <div className={'mm-input-group'}>
                                            <label className={"mm-label"} htmlFor={"backup-network"}> Backup Network </label>
                                            <InputGroup className={"mm-input"}
                                                        id="backup-network"
                                                        onChange={({target: {name, value}}) => this._handleInputChange(name, value)}
                                                        name="backup_network"
                                                        value={center.backup_network || ""} />
                                        </div>

                                        <FormGroup
                                            inline={true}
                                            label="Recommended for registration"
                                            labelFor="recommended"
                                        >
                                            <RadioGroup
                                                inline={true}
                                                onChange={({currentTarget: {value}}) => this._handleInputChange("recomended", value)}
                                                selectedValue={center.recomended}
                                            >
                                                <Radio label="Yes" value="1" />
                                                <Radio label="No" value="0" />
                                            </RadioGroup>
                                        </FormGroup>
                                    </div>
                                </section>
                            </section>

                            <Card elevation={Elevation.TWO} className="Deployment">
                                <header className="Header">
                                    <span>Deployment Information</span>

                                    

                                    <ButtonGroup>
                                        <Popover position={Position.BOTTOM_RIGHT} interactionKind={PopoverInteractionKind.CLICK_TARGET_ONLY}>
                                            <button className="bp3-button bp3-intent-primary bp3-icon-plus"/>
                                            <div style={{ padding: '.5rem' }} className="deployment__pop">
                                                <label className="bp3-label">
                                                    Start Date
                                                    <DateInput 
                                                        onChange={(date) => this.setState({deployment: {...this.state.deployment, startDate: date}})}
                                                        value={this.state.deployment.startDate}
                                                        parseDate={str => new Date(str)}
                                                        formatDate={date => date.toDateString()}/>
                                                </label>
                                                <label className="bp3-label">
                                                    End Date
                                                    <DateInput 
                                                        onChange={(date) => this.setState({deployment: {...this.state.deployment, endDate: date}})}
                                                        value={this.state.deployment.endDate}
                                                        parseDate={str => new Date(str)}
                                                        formatDate={date => date.toDateString()}/>
                                                </label>
                                                <label className="bp3-label">
                                                    Printers
                                                    <MultipleLongList
                                                        defaultValues={deployment.printers}
                                                        onChange={this._handlePrintersOnChange}
                                                        items={printers}/>
                                                </label>
                                                <label className="bp3-label">
                                                    MRWs
                                                    <MultipleLongList
                                                        defaultValues={deployment.machines}
                                                        onChange={this._handleMachinesOnChange}
                                                        items={machines}/>
                                                </label>
                                                <Button
                                                    disabled={disableDeploymentButton}
                                                    loading={editingCenter}
                                                    onClick={() => this._handleCenterDeployment()}
                                                    intent={Intent.PRIMARY}
                                                    icon="tick"
                                                    text="save"
                                                />
                                            </div>
                                        </Popover>
                                    </ButtonGroup>

                                </header>
                                <div className="Table__Content">
                                    <DeploymentTable data={center.deployments}/>
                                </div>
                            </Card>

                        </div>
                    )
                }
            </Card>
        );
    }

    _handleCenterDeployment = () => {
        const {deployment: {startDate, endDate, machines, printers}, center: {id}} = this.state;
        const {authUser: {token}, editCenter} = this.props;

        let data = new FormData();
        data.append("start_date", format(startDate, 'X'));
        data.append("end_date", format(endDate, 'X'));
        for (let i = 0; i < machines.length; i++) {
            data.append('machines[]', machines[i].id);
        }
        for (let i = 0; i < printers.length; i++) {
            data.append('printers[]', printers[i].id);
        }
        data.append("deployment", "1");
        data.append("center_id", id);
        data.append("status", "1");

        editCenter(token, id, data);
    };

    _handlePrintersOnChange = printers => {
        this.setState({deployment: {...this.state.deployment, printers}});
    }

    _handleMachinesOnChange = machines => {
        this.setState({deployment: {...this.state.deployment, machines}});
    }

    static propTypes = {
        authUser: PropTypes.object.isRequired,

        OPERATION_SUCCESSFUL: PropTypes.object.isRequired,
        OPERATION_FAILED: PropTypes.object.isRequired,

        centerId: PropTypes.string.isRequired,

        center: PropTypes.object.isRequired,

        loadingCenter: PropTypes.bool.isRequired,
        editingCenter: PropTypes.bool.isRequired,
        deletingCenter: PropTypes.bool.isRequired,

        loadCenter: PropTypes.func.isRequired,
        editCenter: PropTypes.func.isRequired,
        deleteCenter: PropTypes.func.isRequired,

        machines: PropTypes.array.isRequired,
        printers: PropTypes.array.isRequired,
    };
}


const mapStateToProps = ({authUser, OPERATION_SUCCESSFUL, OPERATION_FAILED, match, history, location, center, loadingCenter, editingCenter, 
    deletingCenter, printers, machines}) => (
        {authUser, OPERATION_SUCCESSFUL, OPERATION_FAILED, match, history, location, center, loadingCenter, editingCenter, 
            deletingCenter, printers, machines});

const mapDispatchToProps = dispatch => bindActionCreators({loadCenter, editCenter, deleteCenter}, dispatch);

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Center));


